import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import createAxiosInstance from "../../config/api";
import "../../config/index.css";
import InstitutionSchedule from "../utils/InstitutionSchedule";

function PersonneScheduleRegi() {
  const { Id } = useParams();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [formData, setFormData] = useState({});
  // const today = new Date();  //  Date  .
  // const todayDate = today.toISOString().split("T")[0]; //   (YYYY-MM-DD)
  // const todayTime = today.toLocaleTimeString('ko-KR', { hour: '2-digit', minute: '2-digit' }); //  ( )

  const handleSave = async (e) => {
    e.preventDefault(); //   
    const confirmSave = window.confirm("  ?");
    if (!confirmSave) {
      return;
    } else {
      setLoading(true);
      setError("");
      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.post(
            `/personnel/institution/${Id}/schedule/add`,
            formData
        );
        window.alert("  ");
        setFormData({});
        navigate(-1);
      } catch (err) {
        if (err.response && err.response.status === 404) {
          // 404   
          window.alert("     ");
        } else {
          //    
          setError("  .");
        }
      }
      finally {
        setLoading(false);
      }
    }
  };


  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevItem) => ({
      ...prevItem,
      [name]: value,
    }));
  };

  const handleDateChange = (e, type) => {
    const dateValue = e.target.value;
    setFormData((prev) => {
      const time = prev[type]?.split("T")[1] || "00:00"; //     
      return {
        ...prev,
        [type]: `${dateValue}T${time}`, //    
      };
    });
  };

  const handleTimeChange = (e, type) => {
    const timeValue = e.target.value;
    setFormData((prev) => {
      const date = prev[type]?.split("T")[0] || new Date().toISOString().split("T")[0]; //     
      return {
        ...prev,
        [type]: `${date}T${timeValue}`, //   
      };
    });
  };

  const handleGoBack = () => {
    navigate(-1);
  };

  if (loading) return <p style={styles.errorMessage}>Loading...</p>;
  if (error) return <p style={styles.errorMessage}>{error}</p>;

  return (
      <div className="detail-container">
            <div style={styles.card}>
              <div style={styles.cardHeader}>
                <h3>   </h3>
              </div>
              <div style={styles.cardBody}>
                <form onSubmit={handleSave}>
                  <div className="form-group">
                    <label> </label>
                    <input
                        name="name"
                        value={formData.name}
                        onChange={handleChange}
                        className="input"
                        required
                    />
                  </div>
                  <div className="form-group">
                    <label></label>
                    <textarea
                        type="text"
                        value={formData.scheduleInfo}
                        name="scheduleInfo"
                        onChange={handleChange}
                        className="input"
                        required
                    />
                  </div>
                  <div className="form-group">
                    <label>, </label>
                    <div style={{ display: "flex", gap: "10px", alignItems: "center" }}>
                      {/*   */}
                      <input
                          type="date"
                          name="startDateTime"
                          value={formData.startDate}
                          // startDateTime   
                          onChange={(e) => handleDateChange(e, "startDateTime")}
                          className="input"
                          required
                      />
                      {/*   */}
                      <input
                          type="time"
                          name="startDateTime"
                          value={formData.startTime}
                          onChange={(e) => handleTimeChange(e, "startDateTime")}
                          className="input"
                          required
                      />
                    </div>
                  </div>
                  <div className="form-group">
                    <label>, </label>
                    <div style={{ display: "flex", gap: "10px", alignItems: "center" }}>
                      {/*   */}
                      <input
                          type="date"
                          name="endDateTime"
                          value={formData.endTime}
                          onChange={(e) => handleDateChange(e, "endDateTime")}
                          className="input"
                          required
                      />
                      {/*   */}
                      <input
                          type="time"
                          name="endDateTime"
                          value={formData.endTime}
                          onChange={(e) => handleTimeChange(e, "endDateTime")}
                          className="input"
                          required
                      />
                    </div>
                  </div>
                  <div className="form-group">
                    <label> </label>
                    <select
                        name="scheduleType"
                        onChange={handleChange}
                        className="input"
                        value={formData.scheduleType}
                        required
                    >
                      <option value="" disabled>
                           
                      </option>
                      <option value=""></option>
                      <option value=""></option>
                      <option value=""></option>
                      <option value=""></option>
                      <option value=""></option>
                    </select>
                  </div>
                  <button type="submit" className="submit-button">
                    
                  </button>
                </form>
                <button className="submit-button" onClick={handleGoBack}>
                  
                </button>
              </div>
            </div>
      </div>
  );
}

const styles = {
  container: {
    padding: "20px",
    max_width: "600px",
    margin: "0 auto",
    background: "#f9f9f9",
  },
  card: {
    boxShadow: "0 2px 8px rgba(0,0,0,0.1)",
    borderRadius: "4px",
    overflow: "hidden",
  },
  cardHeader: {
    backgroundColor: "#007bff",
    color: "#fff",
    padding: "10px",
  },
  cardBody: {
    padding: "20px",
  },
  cardFooter: {
    padding: "10px",
    textAlign: "right",
    borderTop: "1px solid #ddd",
  },
  formGroup: {
    marginBottom: "15px",
  },
  label: {
    fontWeight: "bold",
    display: "block",
    marginBottom: "5px",
  },
  input: {
    width: "100%",
    padding: "8px",
    boxSizing: "border-box",
  },
  box: {
    padding: "10px",
    backgroundColor: "#f5f5f5",
    border: "1px solid #ddd",
    borderRadius: "4px",
  },
  errorMessage: {
    color: "red",
    textAlign: "center",
    margin: "20px",
  },
  actionButton: {
    padding: "8px 12px",
    margin: "0 5px",
    backgroundColor: "rgb(76, 175, 80)",
    color: "#fff",
    border: "none",
    borderRadius: "4px",
    cursor: "pointer",
  },
  actionButton2: {
    padding: "8px 12px",
    margin: "0 5px",
    backgroundColor: "rgb(76, 175, 80)",
    color: "#fff",
    border: "none",
    borderRadius: "4px",
    cursor: "pointer",
    display: "block",
    marginLeft: "auto"
  },
  actionButtonDanger: {
    backgroundColor: "rgb(76, 175, 80)",
  },
};

export default PersonneScheduleRegi;
