import React from "react";
import { Link, useNavigate } from "react-router-dom";
import styled from "styled-components";
import { useState } from "react";
import { useAuth } from "./AuthContext";
import "./index.css";

//  
const Navbar = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const navigate = useNavigate(); // useNavigate  
  const { isLoggedIn, logout } = useAuth(); // useAuth     logout  

  //   
  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleLoginLogout = () => {
    logout(); //  
    navigate("/"); //   
  };

  const handleLogoClick = () => {
    navigate("/dashboard");
  }

  return (
    <>
      <Nav>
        <NavContainer>
          <Logo onClick={handleLogoClick}>WEAVUS</Logo>
          <MenuIcon onClick={toggleMenu}>
            {isMenuOpen ? "" : ""}
          </MenuIcon>
          <Menu $isOpen={isMenuOpen}>
            {isLoggedIn ? (
              <>
                <MenuItem>
                  <StyledLink to="/personnel/dashboard"></StyledLink>
                </MenuItem>
                <MenuItem>
                  <StyledLink to="/dashboard"> </StyledLink>
                </MenuItem>
                <MenuItem>
                  <StyledLink to="/year/dashboard"> </StyledLink>
                </MenuItem>
                <MenuItem>
                  <StyledLink to="/employee"> </StyledLink>
                </MenuItem>
                <MenuItem>
                  <StyledLink to="/admin"></StyledLink>
                </MenuItem>
                <MenuItem>
                  <button className="save-button" onClick={handleLoginLogout}>
                    
                  </button>
                </MenuItem>
              </>
            ) : (
              <MenuItem>
                <button className="save-button" onClick={handleLoginLogout}>
                  
                </button>
              </MenuItem>
            )}
          </Menu>
        </NavContainer>
      </Nav>
      <ContentArea>{}</ContentArea>
    </>
  );
};

// 
const Nav = styled.nav`
  background-color: #333;
  padding: 10px 20px;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 10;
`;

const NavContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
`;

const Logo = styled.h1`
  color: white;
  font-size: 24px;
  width: 18%;
`;

const MenuIcon = styled.div`
  display: none;
  color: white;
  font-size: 18px;
  cursor: pointer;
  width: 50%;
  @media (max-width: 782px) {
    display: block;
  }
`;

const Menu = styled.ul`
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  align-items: center;
  flex-wrap: wrap; /*        */
  width: 70%; /*   100%  */
  justify-content: flex-end; /*     */

  @media (max-width: 782px) {
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    width: 100%;
    background-color: #333;
    flex-direction: column;
    align-items: center;
    height: ${(props) => (props.$isOpen ? "auto" : "0")};
    overflow: hidden;
    transition: height 0.3s ease-in-out;
  }
`;

const MenuItem = styled.li`
  padding: 10px 20px;
`;

const StyledLink = styled(Link)`
  text-decoration: none;
  color: white;
  font-size: 16px;
  text-align: center;
  &:hover {
    color: #ccc;
  }
`;

const ContentArea = styled.div`
  padding-top: 60px; /*         */
`;

export default Navbar;
