import { useState } from "react";
import createAxiosInstance from "../../config/api";

export function useFetchYearData(year) {
  const [error, setError] = useState(""); //  
  const [loading, setLoading] = useState(false); //  
  const [totalAccumulatedAmount, setTotalAccumulatedAmount] = useState(0); //   

  const fetchDataByYear = async (year) => {
    setError(null); //   
    if (!year || isNaN(year)) {
      setError("  .");
      return;
    }
    setLoading(true); //  
    //    
    try {
      const axiosInstance = createAxiosInstance(); //  
      const response = await axiosInstance.get(
        `/employees/accruals/year/${year}`
      );

      setTotalAccumulatedAmount(response.data); //    
    } catch (error) {
      if (error.response) {
        console.error(" :", error.response.status, error.response.data);
        setError("  .");
      } else if (error.request) {
        console.error(" :", error.request);
        setError("  .");
      } else {
        console.error("  :", error.message);
        setError("  .");
      }
    } finally {
      setLoading(false); //  
    }
  };

  return {
    error,
    loading,
    totalAccumulatedAmount,
    fetchDataByYear,
  };
}
