import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import createAxiosInstance from "../../config/api";
import "../../config/index.css";
import InstitutionSchedule from "../utils/InstitutionSchedule";

function PersonneScheduleEdit() {
  const { Id } = useParams();
const { scheduleId } = useParams();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [editedItem, setEditedItem] = useState({});


  useEffect(() => {
    const fetchPersonnel = async () => {
      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.get(`/personnel/institution/${Id}/schedule/${scheduleId}`);
        setEditedItem(response.data);
      } catch (err) {
        setError("  .");
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    fetchPersonnel();
  }, [Id]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setEditedItem((prevItem) => ({
      ...prevItem,
      [name]: value,
    }));
  };

  const handleSave = async (e) => {
    e.preventDefault(); //   
    const confirmSave = window.confirm("  ?");
    if (!confirmSave) {
      return;
    } else {
      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.put(
          `/personnel/institution/${Id}/schedule/${scheduleId}`,
          editedItem
        );
        window.alert("  ");
        navigate(-1);
      } catch (err) {
        if (err.response && err.response.status === 404) {
          // 404   
          window.alert("     ");
        } else {
          //    
          setError("    .");
        }
      }
    }
  };

  const handleGoBack = () => {
    navigate(-1);
  };

  if (loading) return <p style={styles.errorMessage}>Loading...</p>;
  if (error) return <p style={styles.errorMessage}>{error}</p>;

  return (
    <div className="detail-container">
      {editedItem && (
            <div style={styles.card}>
          <div style={styles.cardHeader}>
            <h3> {editedItem.name}  </h3>
          </div>
          <div style={styles.cardBody}>
            <form onSubmit={handleSave}>
              <div className="form-group">
                <label> </label>
                <input
                    name="name"
                    value={editedItem.name}
                    onChange={handleChange}
                    className="input"
                    required
                />
              </div>
              <div className="form-group">
                <label></label>
                <textarea
                    type="text"
                    name="scheduleInfo"
                    value={editedItem.scheduleInfo}
                    onChange={handleChange}
                    className="input"
                    required
                />
              </div>
              <div className="form-group">
                <label>, </label>
                <div style={{ display: "flex", gap: "10px", alignItems: "center" }}>
                  {/*   */}
                  <input
                      type="date"
                      name="startDateTime"
                      value={editedItem.startDateTime?.split("T")[0] || ""}
                      // startDateTime   
                      onChange={(e) => {
                        const dateValue = e.target.value;
                        setEditedItem((prev) => {
                          const time = prev.startDateTime?.split("T")[1] || "00:00";
                          //     
                          return {
                            ...prev,
                            startDateTime: `${dateValue}T${time}`,
                            //    
                          };
                        });
                      }}
                      className="input"
                      required
                  />
                  {/*   */}
                  <input
                      type="time"
                      name="startDateTime"
                      value={editedItem.startDateTime
                          ? editedItem.startDateTime.split("T")[1].substring(0, 5) // :  ( )
                          : "00:00"} // 
                      onChange={(e) => {
                        const timeValue = e.target.value;
                        setEditedItem((prev) => {
                          const date = prev.startDateTime?.split("T")[0] || new Date().toISOString().split("T")[0]; //     
                          return {
                            ...prev,
                            startDateTime: `${date}T${timeValue}`, //    
                          };
                        });
                      }}
                      className="input"
                      required
                  />
                </div>
              </div>
              <div className="form-group">
                <label>, </label>
                <div style={{ display: "flex", gap: "10px", alignItems: "center" }}>
                  {/*   */}
                  <input
                      type="date"
                      name="endDateTime"
                      value={editedItem.endDateTime?.split("T")[0] || ""}
                      onChange={(e) => {
                        const dateValue = e.target.value;
                        setEditedItem((prev) => {
                          const time = prev.endDateTime?.split("T")[1] || "00:00"; //     
                          return {
                            ...prev,
                            endDateTime: `${dateValue}T${time}`, //    
                          };
                        });
                      }}
                      className="input"
                      required
                  />
                  {/*   */}
                  <input
                      type="time"
                      name="endDateTime"
                      value={editedItem.endDateTime
                          ? editedItem.endDateTime.split("T")[1].substring(0, 5) // :  ( )
                          : "00:00"} // 
                      onChange={(e) => {
                        const timeValue = e.target.value;
                        setEditedItem((prev) => {
                          const date = prev.endDateTime?.split("T")[0] || new Date().toISOString().split("T")[0]; //     
                          return {
                            ...prev,
                            endDateTime: `${date}T${timeValue}`, //    
                          };
                        });
                      }}
                      className="input"
                      required
                  />
                </div>
              </div>
              <div className="form-group">
                <label></label>
                <select
                    name="scheduleType"
                    value={editedItem.scheduleType}
                    onChange={handleChange}
                    className="input"
                    required
                >
                  <option value="" disabled>
                       
                  </option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
                <button type="submit" className="submit-button">
                  
                </button>
            </form>
            <button className="submit-button" onClick={handleGoBack}>
              
            </button>
          </div>
          </div>
      )}
    </div>
  );
}

const styles = {
  container: {
    padding: "20px",
    max_width: "600px",
    margin: "0 auto",
    background: "#f9f9f9",
  },
  card: {
    boxShadow: "0 2px 8px rgba(0,0,0,0.1)",
    borderRadius: "4px",
    overflow: "hidden",
  },
  cardHeader: {
    backgroundColor: "#007bff",
    color: "#fff",
    padding: "10px",
  },
  cardBody: {
    padding: "20px",
  },
  cardFooter: {
    padding: "10px",
    textAlign: "right",
    borderTop: "1px solid #ddd",
  },
  formGroup: {
    marginBottom: "15px",
  },
  label: {
    fontWeight: "bold",
    display: "block",
    marginBottom: "5px",
  },
  input: {
    width: "100%",
    padding: "8px",
    boxSizing: "border-box",
  },
  box: {
    padding: "10px",
    backgroundColor: "#f5f5f5",
    border: "1px solid #ddd",
    borderRadius: "4px",
  },
  errorMessage: {
    color: "red",
    textAlign: "center",
    margin: "20px",
  },
  actionButton: {
    padding: "8px 12px",
    margin: "0 5px",
    backgroundColor: "rgb(76, 175, 80)",
    color: "#fff",
    border: "none",
    borderRadius: "4px",
    cursor: "pointer",
  },
  actionButton2: {
    padding: "8px 12px",
    margin: "0 5px",
    backgroundColor: "rgb(76, 175, 80)",
    color: "#fff",
    border: "none",
    borderRadius: "4px",
    cursor: "pointer",
    display: "block",
    marginLeft: "auto"
  },
  actionButtonDanger: {
    backgroundColor: "rgb(76, 175, 80)",
  },
};

export default PersonneScheduleEdit;
