import React, { useState } from 'react';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin from '@fullcalendar/interaction';
import "../../config/index.css";

const ScheduleCalendar = ({ schedules }) => {
    const [selectedEvent, setSelectedEvent] = useState(null);

    const events = schedules.map((schedule) => ({
        id: schedule.id,  // ID      
        title: schedule.name,
        start: schedule.startDateTime,
        end: schedule.endDateTime,
        extendedProps: {
            scheduleInfo: schedule.scheduleInfo,
            scheduleType: schedule.scheduleType,
            institutionId: schedule.institutionId,
        },
    }));

    const handleEventClick = (info) => {
        const eventDetails = {
            id: info.event.id,
            title: info.event.title,
            scheduleInfo: info.event.extendedProps.scheduleInfo,
            start: info.event.start ? info.event.start.toLocaleDateString() : '-',
            startTime: info.event.start
                ? info.event.start.toLocaleTimeString('ko-KR', { hour: '2-digit', minute: '2-digit' })
                : '-',
            end: info.event.end ? info.event.end.toLocaleDateString() : '-',
            endTime: info.event.end
                ? info.event.end.toLocaleTimeString('ko-KR', { hour: '2-digit', minute: '2-digit' })
                : '-',
            scheduleType: info.event.extendedProps.scheduleType,
            institutionId: info.event.extendedProps.institutionId,
        };

        setSelectedEvent(eventDetails);

        let periodDisplay = '';
        if (eventDetails.start !== eventDetails.end && eventDetails.startTime === eventDetails.endTime) {
            //  
            periodDisplay = `<p><strong>:</strong> ${eventDetails.start} ~ ${eventDetails.end}</p>`;
        } else if (eventDetails.start === eventDetails.end && eventDetails.startTime !== eventDetails.endTime) {
            //    
            periodDisplay = `<p><strong>:</strong> ${eventDetails.start} ~ ${eventDetails.end}</p>
                             <p><strong>:</strong> ${eventDetails.startTime} ~ ${eventDetails.endTime}</p>`;
        } else {
            //   
            periodDisplay = `<p><strong>:</strong> ${eventDetails.start}</p>
                             <p><strong>:</strong> </p>`;
        }


        //   ,     
        const newWindow = window.open('', '_blank', 'width=600,height=400,left=200,top=200'); //   
        newWindow.document.write(`
            <html>
                <head><title> </title></head>
                <body>
                    <h2> </h2>
                    <p><strong>:</strong> ${eventDetails.title}</p>
                    <p><strong> :</strong> ${eventDetails.scheduleInfo}</p>
                    ${periodDisplay}
                     <button 
                    id="editBtn"
                    style="
                    padding: 12px; 
                    background-color: #007bff; 
                    color: white; 
                    border: none; 
                    border-radius: 4px; 
                    font-size: 16px; 
                    cursor: pointer; 
                    font-weight: bold; 
                    transition: background-color 0.3s ease; 
                    margin: 10px 10px; 
                    text-align: center; 
                    float: right; 
                    flex-shrink: 0;" ></button>
                    <button 
                    style="
                    padding: 12px; 
                    background-color: #4caf50; 
                    color: white; 
                    border: none; 
                    border-radius: 4px; 
                    font-size: 16px; 
                    cursor: pointer; 
                    font-weight: bold; 
                    transition: background-color 0.3s ease; 
                    margin: 10px 10px; 
                    text-align: center; 
                    float: right; 
                    flex-shrink: 0;"
                    onclick="window.close()" ></button>
                    <script>
                        document.getElementById('editBtn').addEventListener('click', function() {
                            const editUrl = 
                            '/#/personnel/institution/${eventDetails.institutionId}/schedule/${eventDetails.id}'; 
                            //   URL
                            window.opener.location.href = editUrl; //   URL 
                            window.close(); //   
                        });
                    </script>
                </body>
            </html>
        `);
    };

    return (
        <div>
            <h3>Schedules</h3>
            <FullCalendar
                plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
                initialView="dayGridMonth"
                headerToolbar={{
                    left: 'prev,next today',
                    center: 'title',
                    right: '',
                }}
                events={events}
                eventClick={handleEventClick}  //    
            />
        </div>
    );
};

export default ScheduleCalendar;
