import React, { useState } from "react";
import { EmployeeRegiApi } from "../utils/EmployeeRegiApi";
import "../../config/index.css";

const EmployeeRegi = () => {
  const { addEmployee, loading, error, responseMessage } = EmployeeRegiApi(); // API  
  const [formData, setFormData] = useState({
    id: "",
    name: "",
    entryDate: "",
    exitDate: "",
    employeeType: "",
    conversionDate: "",
    rank: "",
    status: 0,
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    await addEmployee(formData); // API 
    if (!error) {
      //    
      setFormData({
        id: "",
        name: "",
        entryDate: "",
        exitDate: "",
        employeeType: "",
        conversionDate: "",
        rank: "",
        status: 0,
      });
    }
  };

  return (
    <div className="detail-container">
      <h2 className="title">  </h2>
      {responseMessage && !error && (
        <p className="success-message">{responseMessage}</p>
      )}
      {error && !responseMessage && <p className="error-message">{error}</p>}
      <form onSubmit={handleSubmit} className="employee-regi-form">
        <div className="form-group">
          <label>ID</label>
          <input
            type="text"
            name="id"
            value={formData.id}
            onChange={handleChange}
            required
            className="input"
          />
        </div>
        <div className="form-group">
          <label></label>
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
            className="input"
          />
        </div>
        <div className="form-group">
          <label></label>
          <input
            type="date"
            name="entryDate"
            value={formData.entryDate}
            onChange={handleChange}
            required
            className="input"
          />
        </div>
        <div className="form-group">
          <label> ()</label>
          <input
            type="date"
            name="exitDate"
            value={formData.exitDate}
            onChange={handleChange}
            className="input"
          />
        </div>
        <div className="form-group">
          <label> </label>
          <select
            name="employeeType"
            value={formData.employeeType}
            onChange={handleChange}
            className="input"
            required
          >
            <option value="" disabled>
                 
            </option>
            <option value="CONTRACT"></option>
            <option value="REGULAR"></option>
          </select>
        </div>
        {formData.employeeType === "REGULAR" && (
          <div className="form-group">
            <label> ( )</label>
            <input
              type="date"
              name="conversionDate"
              value={formData.conversionDate}
              onChange={handleChange}
              required
              className="input"
            />
          </div>
        )}
        <div className="form-group">
          <label></label>
          <select
            name="rank"
            value={formData.rank}
            onChange={handleChange}
            className="input"
            required
          >
            <option value="" disabled>
                
            </option>
            <option value={0}></option>
            <option value={1}></option>
            <option value={2}></option>
            <option value={3}></option>
            <option value={4}></option>
          </select>
        </div>
        <button type="submit" disabled={loading} className="submit-button">
          {loading ? " ..." : " "}
        </button>
      </form>
    </div>
  );
};

export default EmployeeRegi;
