import React from "react";
import { useNavigate } from "react-router-dom";
import { getEmployeeTypeText } from "../../utils/textUtils";
import { useState, useEffect } from "react";
import "../../config/index.css";
import createAxiosInstance from "../../config/api";

function EmployeeDashboard() {
  const navigate = useNavigate();
  const [data, setData] = useState([]);
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const [isFetched, setIsFetched] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      if (isFetched) return; //    

      setLoading(true); //  

      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.get("/employees/lists");
        const sortedData = response.data.sort((a, b) => {
          const dateA = new Date(a.entryDate); // entryDate Date  
          const dateB = new Date(b.entryDate);
          return dateA - dateB; //   (  )
        });
        if (Array.isArray(sortedData)) {
          setData(sortedData);
        } else {
          setError("     .");
        }
        setIsFetched(true); //   
      } catch (error) {
        if (error.response) {
          console.error(
            " :",
            error.response.status,
            error.response.data
          );
          setError("  .");
        } else if (error.request) {
          console.error(" :", error.request);
          setError("  .");
        } else {
          console.error("  :", error.message);
          setError("  .");
        }
      } finally {
        setLoading(false); //  
      }
    };

    fetchData();
  }, [isFetched]);

  const handleRowClick = () => {
    navigate(`/employee/new`);
  };

  const handleEmployeeClick = (employeeId) => {
    navigate(`/employee/${employeeId}`); //   
  };

  return (
    <div className="container">
      <h1 className="title">WEAVUS   </h1>
      <button className="submit-button" onClick={() => handleRowClick()}>
         
      </button>
      {loading && <div className="loading">Loading...</div>}
      {error && <div className="error-message">{error}</div>}
      {!loading && !error && (
        <table className="table">
          <thead>
            <tr>
              <th className="table-header"></th>
              <th className="table-header"></th>
              <th className="table-header"> </th>
            </tr>
          </thead>
          <tbody>
            {data.map((employee) => (
              <tr key={employee.id}>
                <td className="table-data">
                  <button
                    className="action-button"
                    onClick={() => handleEmployeeClick(employee.id)} //   handleEmployeeClick  
                  >
                    {employee.name}
                  </button>
                </td>
                <td className="table-data">{employee.entryDate}</td>
                <td className="table-data">
                  {getEmployeeTypeText(employee.employeeType, employee.status)}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}

export default EmployeeDashboard;
