import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { getEmployeeTypeText, getRankText } from "../../utils/textUtils";
import createAxiosInstance from "../../config/api";
import "../../config/index.css";

function EmployeeDetail() {
  const { employeeId } = useParams();
  const navigate = useNavigate();
  const [item, setItem] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [isEditing, setIsEditing] = useState(false);
  const [editedItem, setEditedItem] = useState({});

  useEffect(() => {
    const fetchEmployee = async () => {
      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.get(`/employees/${employeeId}`);
        setItem(response.data);
        setEditedItem(response.data);
      } catch (err) {
        setError("   .");
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    fetchEmployee();
  }, [employeeId]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setEditedItem((prevItem) => ({
      ...prevItem,
      [name]: value,
    }));
  };

  const handleSave = async () => {
    const confirmSave = window.confirm("  ?");
    if (!confirmSave) {
      return;
    } else {
      try {
        if (editedItem.rank === "" && editedItem.employeeType === "") {
          alert("   .    ."); //      
          return;
        }
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.put(
          `/employees/${employeeId}`,
          editedItem
        );
        setItem(response.data);
        setIsEditing(false);
        window.alert("  ");
      } catch (err) {
        if (err.response && err.response.status === 404) {
          // 404   
          window.alert("     ");
        } else {
          //    
          setError("    .");
        }
      }
    }
  };

  const handleEditClick = () => {
    setIsEditing(true);
  };

  const handleGoBack = () => {
    navigate(-1);
  };

  //       
  const handleDelete = async (id) => {
    if (editedItem.exitDate == null) {
      const confirmDelete = window.confirm("   . \n     ?");
      if (!confirmDelete) return;
      editedItem.exitDate = new Date().toISOString().split("T")[0];
      await handleSave(editedItem); //   
    }else {
      const confirmDelete = window.confirm(" ?");
      if (!confirmDelete) return;
    }

    try {
      const axiosInstance = createAxiosInstance(); //  
      const response = await axiosInstance.post(
          `/employees/${id}`
      );
      if (response.status === 200) {
        alert("  .");
        navigate("/employee"); //      
      } else if (response.status === 404) {
        alert("   .");
      }
    } catch (err) {
      alert("  .");
      console.error(err);
    }
  };

  if (loading) return <p style={styles.errorMessage}>Loading...</p>;
  if (error) return <p style={styles.errorMessage}>{error}</p>;

  return (
    <div className="detail-container">
      {item && (
        <div style={styles.card}>
          <div style={styles.cardHeader}>
            <h3>{item.name} </h3>
          </div>
          <div style={styles.cardBody}>
            {isEditing ? (
              <>
                <div style={styles.formGroup}>
                  <label style={styles.label}> </label>
                  <input
                    style={styles.input}
                    name="id"
                    value={editedItem.id}
                    onChange={handleChange}
                    disabled
                  />
                </div>
                <div style={styles.formGroup}>
                  <label style={styles.label}> </label>
                  <input
                    style={styles.input}
                    name="name"
                    value={editedItem.name}
                    onChange={handleChange}
                    required
                  />
                </div>
                <div style={styles.formGroup}>
                  <label style={styles.label}></label>
                  <input
                    type="date"
                    style={styles.input}
                    name="entryDate"
                    value={editedItem.entryDate}
                    onChange={handleChange}
                    required
                  />
                </div>
                <div style={styles.formGroup}>
                  <label style={styles.label}></label>
                  <input
                    type="date"
                    style={styles.input}
                    name="exitDate"
                    value={editedItem.exitDate || ""}
                    onChange={handleChange}
                  />
                </div>
                <div style={styles.formGroup}>
                  <label style={styles.label}> </label>
                  <select
                    style={styles.input}
                    name="employeeType"
                    value={editedItem.employeeType}
                    onChange={handleChange}
                    className="input"
                    required
                  >
                    <option value="CONTRACT"></option>
                    <option value="REGULAR"></option>
                  </select>
                </div>
                {editedItem.employeeType === "REGULAR" && (
                    <div style={styles.formGroup}>
                      <label style={styles.label}> </label>
                      <input
                          type="date"
                          style={styles.input}
                          name="conversionDate"
                          value={editedItem.conversionDate|| ""}
                          onChange={handleChange}
                          required
                          className="input"
                      />
                    </div>
                )}
                <div style={styles.formGroup}>
                  <label style={styles.label}></label>
                  <select
                    style={styles.input}
                    name="rank"
                    value={editedItem.rank}
                    onChange={handleChange}
                    required
                  >
                    <option value={0}></option>
                    <option value={1}></option>
                    <option value={2}></option>
                    <option value={3}></option>
                    <option value={4}></option>
                  </select>
                </div>
              </>
            ) : (
              <>
                <div style={styles.formGroup}>
                  <label style={styles.label}> </label>
                  <div style={styles.box}>
                    <p>{item.id}</p>
                  </div>
                </div>
                <div style={styles.formGroup}>
                  <label style={styles.label}> </label>
                  <div style={styles.box}>
                    <p>{item.name}</p>
                  </div>
                </div>
                <div style={styles.formGroup}>
                  <label style={styles.label}></label>
                  <div style={styles.box}>
                    <p>{item.entryDate}</p>
                  </div>
                </div>
                <div style={styles.formGroup}>
                  <label style={styles.label}></label>
                  <div style={styles.box}>
                    <p>{item.exitDate ? item.exitDate : ""}</p>
                  </div>
                </div>
                <div style={styles.formGroup}>
                  <label style={styles.label}> </label>
                  <div style={styles.box}>
                    <p>
                      {" "}
                      {getEmployeeTypeText(item.employeeType, item.status)}
                    </p>
                  </div>
                </div>
                <div style={styles.formGroup}>
                  <label style={styles.label}> </label>
                  <div style={styles.box}>
                    <p>{item.conversionDate ? item.conversionDate : ""}</p>
                  </div>
                </div>
                <div style={styles.formGroup}>
                  <label style={styles.label}></label>
                  <div style={styles.box}>
                    <p>{getRankText(item.rank)}</p>
                  </div>
                </div>
              </>
            )}
          </div>
          <div style={styles.cardFooter}>
            {isEditing ? (
              <button style={styles.actionButton} onClick={handleSave}>
                
              </button>
            ) : (
              <button style={styles.actionButton} onClick={handleEditClick}>
                
              </button>
            )}
            {!isEditing && (
              <button
                style={{ ...styles.actionButton, ...styles.actionButtonDanger }}
                onClick={() => handleDelete(item.id)}
              ></button>
            )}
            <button style={styles.actionButton} onClick={handleGoBack}>
              
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

const styles = {
  container: {
    padding: "20px",
    max_width: "600px",
    margin: "0 auto",
    background: "#f9f9f9",
  },
  card: {
    boxShadow: "0 2px 8px rgba(0,0,0,0.1)",
    borderRadius: "4px",
    overflow: "hidden",
  },
  cardHeader: {
    backgroundColor: "#007bff",
    color: "#fff",
    padding: "10px",
  },
  cardBody: {
    padding: "20px",
  },
  cardFooter: {
    padding: "10px",
    textAlign: "right",
    borderTop: "1px solid #ddd",
  },
  formGroup: {
    marginBottom: "15px",
  },
  label: {
    fontWeight: "bold",
    display: "block",
    marginBottom: "5px",
  },
  input: {
    width: "100%",
    padding: "8px",
    boxSizing: "border-box",
  },
  box: {
    padding: "10px",
    backgroundColor: "#f5f5f5",
    border: "1px solid #ddd",
    borderRadius: "4px",
  },
  errorMessage: {
    color: "red",
    textAlign: "center",
    margin: "20px",
  },
  actionButton: {
    padding: "8px 12px",
    margin: "0 5px",
    backgroundColor: "rgb(76, 175, 80)",
    color: "#fff",
    border: "none",
    borderRadius: "4px",
    cursor: "pointer",
  },
  actionButtonDanger: {
    backgroundColor: "rgb(76, 175, 80)",
  },
};

export default EmployeeDetail;
