
//     
export const getStateText = (state) => {
  switch (state) {
    case 0:
      return " ";
    case 1:
      return " ";
    case 2:
      return " ";
    default:
      return "  ";
  }
};

//     
export const getEmployeeTypeText = (employeeType, status, exitDate) => {
  if (status == 0 && exitDate ==null) {
    switch (employeeType) {
      case "CONTRACT":
        return "";
      case "REGULAR":
        return "";
      default:
        return "  ";
    }
  } else {
    return "";
  }
};

//    
export const getRankText = (rank) => {
  switch (rank) {
    case 0:
      return "";
    case 1:
      return "";
    case 2:
      return "";
    case 3:
      return "";
    case 4:
      return "";
    default:
      return "  ";
  }
};

export const formatAmount = (amount) => {
  return new Intl.NumberFormat("ja-JP", {
    style: "currency",
    currency: "JPY",
  }).format(amount);
};
