import React, { useState } from "react";
import createAxiosInstance from "../../config/api";
import {useParams} from "react-router-dom";
export { FileUploadDownload as default };

export function FileUploadDownload(editedItem) {

  const { Id } = useParams();
  const [uploading, setUploading] = useState(false);
  const [progress, setProgress] = useState(0); //   
  const [downloading, setDownloading] = useState(false);
  const [files, setFiles] = useState({ resume1: [], resume2: [], resume3: [] }); //    



  const handleFileChange = (event, type) => {
    const selectedFiles = Array.from(event.target.files);
    if (
        (editedItem.resume1 !== null && type === "resume1") ||
        (editedItem.resume2 !== null && type === "resume2") ||
        (editedItem.resume3 !== null && type === "resume3")
    ) {
      const confirmUpload = window.confirm(
          "   .  ?"
      );

      if (!confirmUpload) {
        //      
        event.target.value = null;
        return; //   
      }
    }

    setFiles((prevFiles) => ({
      ...prevFiles,
      [type]: selectedFiles, //     
    }));
  };

  const handleUpload = async () => {
    if (files.resume1.length === 0 && files.resume2.length === 0 && files.resume3.length === 0) {
      alert(" .");
      return;
    }

    const formData = new FormData();
    //     
    files.resume1.forEach((file) => {
      formData.append("files", file);
      formData.append("resumeTypes", "resume1");
    });
    // resume2   
    files.resume2.forEach((file) => {
      formData.append("files", file);
      formData.append("resumeTypes", "resume2");
    });
    // resume3   
    files.resume3.forEach((file) => {
      formData.append("files", file);
      formData.append("resumeTypes", "resume3");
    });

    setUploading(true);
    setProgress(0); // 

    try {
      const axiosInstance = createAxiosInstance();
      await axiosInstance.post(`/personnel/applicant/${Id}/upload`, formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
        onUploadProgress: (progressEvent) => {
          const percentCompleted = Math.round(
            (progressEvent.loaded * 100) / progressEvent.total
          );
          setProgress(percentCompleted);
        },
      });
      alert("  !");
      setFiles({ resume1: [], resume2: [], resume3: [] });
    } catch (error) {
      console.error("  :", error);
      alert("    .");
    } finally {
      setUploading(false);
      setProgress(0); //    
    }
  };

  const handleDownload = async () => {
    setDownloading(true);
    try {
      const axiosInstance = createAxiosInstance();

      //   
      const response = await axiosInstance.get(`/personnel/applicant/${Id}/download`, {
        responseType: "blob", //  Blob  
      });

      // Blob URL   
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download",  "_.zip"); //  
      document.body.appendChild(link);
      link.click();
      link.remove();

      //    URL    
      window.URL.revokeObjectURL(url);

    } catch (error) {
      console.error("  :", error);
      alert("    .");
    } finally {
      setDownloading(false);
    }
  };

  return {
    handleFileChange,
    handleUpload,
    handleDownload,
    uploading,
    progress,
    downloading,
    files,
  };
}

